/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
  overflow-x: hidden;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  min-width: 100vw;
  min-height: 100vh;
}
.navRootDesktop {
  width: 100vw;
}
.navRootMobi {
  width: 100vw;
}
.container {
  color: white;
  position: relative;
}
.container form {
  position: relative;
  top: 40vh;
}
form div div button {
  border: 1px solid transparent;
  line-height: 40px;
  padding: 0 30px;
  border-radius: 20px;
  background: transparent;
  border: 1px solid #fff;
  color: #222;
  display: inline-block;
  font-weight: 500;
  position: relative;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  cursor: pointer;
  text-transform: uppercase;
  background: #fff;
  border: 1px solid #eee;
  color: #6cbb23;
  float: right;
  margin-top: 1em;
}
.single-contact h2 {
  font-size: 24px;
  font-weight: 200;
}
.single-contact h2 span {
  font-weight: 600;
}
.col-lg-5 {
  margin-bottom: 1em;
}
.container .justify-content-center {
  position: absolute;
  top: 9vh;
  left: 50vw;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 60vw;
}
@media (max-width: 500px) {
  form .row.justify-content-center {
    left: 44vw;
  }
}
.container.no-padding {
  padding-left: 0;
  padding-right: 0;
}
.justify-content-center {
  text-align: center;
}

.contact-area {
  background-color: #a6d477;
  color: #fff;
}
.contact-area .overlay-bg {
  background: rgba(108, 187, 35, 0.85);
}
.contact-area h2 {
  font-size: 36px;
  font-weight: 100;
}
.contact-area h2 span {
  font-weight: 600;
}
.contact-area p {
  font-size: 14px;
  font-weight: 300;
}
.contact-form ::-webkit-input-placeholder {
  color: #fff;
  font-weight: 300;
}
.contact-form :-moz-placeholder {
  color: #fff;
  opacity: 1;
  font-weight: 300;
}
.contact-form ::-moz-placeholder {
  color: #fff;
  opacity: 1;
  font-weight: 300;
}
.contact-form :-ms-input-placeholder {
  color: #fff;
  font-weight: 300;
}
.contact-form ::-ms-input-placeholder {
  color: #fff;
  font-weight: 300;
}
.contact-form .alert-msg {
  color: #fff !important;
  margin-top: 50px;
  z-index: 1;
}
.common-input {
  border: 1px solid #a6d477;
  line-height: 48px;
  width: 100%;
  display: block;
  background: transparent;
  padding: 0 25px;
  color: #fff;
}
.common-textarea {
  border: 1px solid #a6d477;
  width: 100%;
  display: block;
  background: transparent;
  padding: 15px 25px;
  color: #fff;
  height: 150px;
  resize: none;
}

.col-lg-3 {
  padding-left: 15px;
}
.single-footer-widget h6 {
  color: #000;
  margin-bottom: 20px;
  text-transform: uppercase !important;
  line-height: 1.5em;
}
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container .footer-nav li a {
  color: #777;
  outline: 0;
}
a {
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  text-decoration: none;
}

.single-footer-widget {
  color: #777;
}
.row.pt-60 {
  padding-top: 10em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
@media (max-width: 500px) {
  .row.pt-60 {
    padding-top: 3em;
  }
}
.col-sm-6 {
  -ms-flex: 1 1;
      flex: 1 1;
}
.footer-bottom {
  position: absolute;
  bottom: 10vh;
  left: 5vw;
}
.footer-bottom p span {
  color: #f7a930;
}
.footer-bottom p a {
  color: #f7a930;
}



